                reviewer: 'Buddhist-Christian Studies',
                reviewCN: `>“蒋的书谨慎指出了比较研究方法中存在的潜在问题，这在当前比较研究流行的学术氛围中是很有意义的。所有从事比较研究的学者（即使是那些对佛教瑜伽行派不感兴趣的人）都应该将此书列入他们的阅读清单。”`,
