    about: {
        seoTitle: `About - Tao Jiang`,
        seoTitleCN: `关于蒋韬`,
        seoDescription: `Detail info about Tao Jiang`,
        name: 'Tao Jiang',
        nameCN: '蒋韬',
        details: [
            `I am a scholar of classical Chinese philosophy, Mahāyāna Buddhist philosophy, and cross-cultural philosophy. Currently, I am a professor in the [Department of Religion](https://religion.rutgers.edu/tao-jiang) and an associate member of the graduate faculty in the Department of Philosophy at Rutgers University, New Brunswick, New Jersey, US. `,
            `I grew up in the ancient city of [Chengdu](https://www.nytimes.com/2016/03/06/t-magazine/chengdu-china-travel-guide.html) in China’s southwest, “where the pace is slow, the food is spicy and there’s always time for a good gossip over a cup of tea.” As a boy, I often dreamt of an adventurous life in a faraway place, which probably explains why I came all the way to America to pursue my dream. Although America is far enough from China, an academic life devoted to philosophical inquiry does not sound like much of an adventure. Except it *is* an adventure: a genuine philosophical inquiry is an adventure of the mind in which that very inquisitive mind is its own limit. My approach to philosophy is to treat it as a way to study particular forms of intellectual and moral culture and the people who produce it.`,
            `I am the author of [*Contexts and Dialogue: Yogācāra Buddhism and Modern Psychology on the Subliminal Mind*](/buddhism_and_psychology) and the co-editor of [*The Reception and Rendition of Freud in China: China’s Freudian Slip*](/freud_and_china/). My [articles](/papers/) have appeared in leading Asian and comparative philosophy journals and several major anthologies. My new book, [*Origins of Moral-Political Philosophy in Early China*](/origins_of_chinese_phil/), will be published by Oxford University Press in 2021. I have also been working on several other book projects, including a new interpretation of Linji's Chan/Zen Buddhist philosophy, Buddha Nature in medieval Chinese intellectual history, and a comparative project on free will. `,
            `I am Chair of the [Department of Religion](https://religion.rutgers.edu) and Director of the [Center for Chinese Studies](https://rccs.rutgers.edu) at Rutgers University. I also co-direct the [Rutgers Workshop on Chinese Philosophy](/events) and co-chair the [Neo-Confucian Studies Seminar](https://universityseminars.columbia.edu/seminars/neo-confucian-studies/) at Columbia University. I am currently the co-chair of the [Buddhist Philosophy Unit](https://aar-conference.imis-inspire.com/a/page/ProgramUnits/buddhist-philosophy-unit) under the American Academy of Religion annual meeting. I serve on the editorial boards of *Dao*, *Journal of Chinese Philosophy*, *Confucian Philosophy and Culture*, and *Journal of Buddhist Philosophy*.`,
            `I am married. We enjoy hanging out in coffee shops, museums, parks, and watching movies. We play tennis from time to time and love going to the US Open Tennis Championships. We support all effort dedicated to the saving of the world's wildlife. These amazing videos [“Hungry polar bear surprises a seal”](https://www.youtube.com/watch?v=0mgnf6t9VEc), [“Elephant Calf Starves To Death Due To Drought”](https://www.youtube.com/watch?v=RqeC6XB94zs) from the [BBC Earth series](https://www.facebook.com/bbcearth) have opened up our eyes to the beauty, intelligence and misery of animal lives in the wilderness. Check them out, if you haven't, and your view about wildlife will never be the same again.`
        ],
        detailsCN: [
            `我从事中国先秦哲学、大乘佛教哲学和跨文化哲学的研究，是美国新泽西州罗格斯大学宗教系教授和哲学系兼职教授。`,
            `我在中国西南的古城成都长大，“那里的生活节奏缓慢，美食小吃麻辣爽口，而且人们总有时间边喝茶边闲聊”。小时候，我经常梦想着去一个遥远的地方，这也许就是我为什么万里迢迢来到美国。哲学研究听起来平淡无奇，但其实妙趣无穷：真正的哲学探索是一种心性的体验，在这里，可以与历史上的大德先贤进行深度的心灵交流，也能使自己的思想自由探索。对我来说，哲学是一种研究知性和道德文化及其建构者的有趣途径。`,
            `我作有[《语境与对话：佛教瑜伽行派与现代心理学论潜意识》](/cn/buddhism_and_psychology/)，与艾文贺合编[《弗洛伊德与中国》](/cn/freud_and_china/)。我的新书[《中国先秦道德政治哲学的起源》](/cn/origins_of_chinese_phil/)将于 2021 年 8 月由[牛津大学出版社](https://global.oup.com/academic/product/origins-of-moral-political-philosophy-in-early-china-9780197611364)出版。目前还处于创作阶段的书包括对临济禅宗思想的新解、中国中世纪思想史中的佛性论以及对自由意志的比较研究。`,
            `我是罗格斯大学的[宗教系](https://religion.rutgers.edu)主任和[中国研究中心](https://rccs.rutgers.edu)主任，也是罗格斯大学[中国哲学研讨会](/cn/events)的共同发起人与负责人，并共同主持哥伦比亚大学[新儒家研讨会](https://universityseminars.columbia.edu/seminars/neo-confucian-studies/)，目前还是美国宗教学会的[佛教哲学组](https://aar-conference.imis-inspire.com/a/page/ProgramUnits/buddhist-philosophy-unit)组长。我是《道》、《中国哲学杂志》、《儒家哲学与文化》、《佛教哲学杂志》的编委。`,
        ],
    }
